@use 'reset';
@use 'colors';
@use 'mixins';


header {
  @include mixins.flex();
  background-color: colors.$blue;
  height: 13vh;
  padding: 0 20px 0 20px;

  img {
    height: 65%;
  }

  nav {
    //@include mixins.flex();
    font-size: 1.5em;
    width: 80%;

    ul {
      @include mixins.flex();
    }

  }

}

main {
  position: absolute;
  
  section {
    position: relative;
  }
  
}

